import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Login from '@/components/Login'
import Table from '@/components/pages/Table'
import Table2 from '@/components/pages/Table2'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/',
      name: 'index',
      component: Index,
      children: [
          { path: '/danhsach', component: Table, name: 'Danh sách phòng' },
          { path: '/danhsachdat', component: Table2, name: 'Danh sách đặt' }
      ]
    }
  ]
})



