<template>
  <transition name="fade" mode="out-in">
    <router-view></router-view>
  </transition>
</template>

<script>
import firebase from 'firebase'

import CsvExport from './assets/utils/CsvExport'

import { mapGetters } from 'vuex'


let config = {
  apiKey: "AIzaSyDZAdyrs9t1m_qx4jvq2SlRIAxifBeiZ68",
  authDomain: "hotel-c24ce.firebaseapp.com",
  databaseURL: "https://hotel-c24ce.firebaseio.com",
  projectId: "hotel-c24ce",
  storageBucket: "hotel-c24ce.appspot.com",
  messagingSenderId: "933087622720"
};

firebase.initializeApp(config)


export default {
  data() {
    return {

    }
  },
  components: {
  },
  methods: {

  },
  computed: {
    ...mapGetters(['getUserSignIn'])
  },
  created() {
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.dispatch('userSignIn', user.email);
        this.$router.push('/danhsachdat');
      } else {
        this.$router.push('/login');
        this.$store.dispatch('userSignIn', '');
      }
    });
  },

}
</script>

<style lang="scss">
@import 'assets/animate.css';
.animated {
  animation-duration: .377s;
}



body {
  font-family: Helvetica, Arial, sans-serif;
  margin: 0;
}

a {
  text-decoration: none;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity .5s
}

.fade-enter,
.fade-leave-to
/* .fade-leave-active in <2.1.8 */

{
  opacity: 0
}

#app {
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.wrapper {
  overflow-x: hidden !important;
}

.mainNav {
  background-color: rgb(32, 160, 255);
  color: white;
  padding: 25px 15px;
  display: flex;
  flex-flow: row wrap;
}

.mainNav-logo {
  flex: 0 0 auto;
  a {
    display: block;
    padding: 0 15px;
    color: white;
  }
}

.mainNav-list {
  flex: 1 1 0px;
  text-align: right;
  padding: 0 15px;
  a {
    margin-left: 20px;
    color: white;
  }
}

.HolyGrail {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}


html,
body {
  height: 100%;
  background-color: #f8f8f8;
}

.HolyGrail-body {
  display: flex;
  flex: 1;
}

.HolyGrail-nav {
  flex: 0 1 auto;
  background-color: #eef1f6;
}

.HolyGrail-content {
  flex: 1;
  padding: 30px 20px;
}
</style>



