import Vue from 'vue'
import Vuex from 'vuex'

import * as actions from './actions'
import * as getters from './getters'

Vue.use(Vuex); // have $store for all Vue component

import {
  USER_SIGN_IN
} from './mutation-types'

const state = {
  userSignIn: ''
}
 //The only way to change state in Vuex store is by committing a mutation.
const mutations = {
  [USER_SIGN_IN](state, status) {
    state.userSignIn = status
  }
}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



