<template>
    <div class="">
    
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
                <el-form-item>
                    <el-input placeholder="Từ khóa"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary">Tìm kiếm</el-button>
                </el-form-item>
    
            </el-form>
        </el-col>
    
        <el-table :data="tableData" style="width: 100%">
            <el-table-column label="STT" prop="stt" sortable>
            </el-table-column>
    
            <el-table-column label="Thời gian" prop="time" sortable>
            </el-table-column>
    
            <el-table-column label="Tên phòng" prop="roomName">
            </el-table-column>
    
            <el-table-column label="Ngày đến" prop="date1" sortable>
            </el-table-column>
    
            <el-table-column label="Ngày đi" prop="date2" sortable>
            </el-table-column>

            <el-table-column label="Người lớn" prop="adults" sortable>
            </el-table-column>

            <el-table-column label="Trẻ em" prop="children" sortable>
            </el-table-column>
    
        </el-table>
    
    </div>
</template>

<script>

import firebase from 'firebase';

let db = firebase.database()
let roomOrder = db.ref().child('orderRooms');

export default {
    firebase: {
        rooms: roomOrder
    },
    data() {
        return {
            editFormVisible: false
        }
    },
    components: {
    },
    methods: {

    },
    computed: {
        tableData: function () {
            var roomsNew = this.rooms;
            var i = 1;
            roomsNew.forEach(function (e) {
                e.key = e['.key'];
                e.time = new Date(e.time).toLocaleString();
                e.stt = i++;
            })
            return roomsNew;
        },
    }

}
</script>

<style lang="scss">
.list-inline {
    margin: 20px 0;
    list-style: none;
    padding-left: 0;
    li {
        display: inline-block;
        margin: 0 10px;
    }
}
</style>



