<template>
    <div class="">
    
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true">
                <el-form-item>
                    <el-input placeholder="Từ khóa"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary">Tìm kiếm</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary">Tạo mới</el-button>
                </el-form-item>
            </el-form>
        </el-col>
    
        <el-table :data="tableData" style="width: 100%">
            <el-table-column type="expand">
                <template scope="props">
                    <p>
                        <b>Mô tả:</b>
                        <br/>
                        <div v-html="props.row.body_html"></div>
                    </p>
                    <p>
                        <b>Hình ảnh chính: </b>
                        <br/>
    
                        <img v-bind:src="props.row.image_feature" width="300" />
    
                    </p>
                    <p>
                        <b>DS hình ảnh:</b>
                        <ul class="list-inline">
                            <li v-for="image in props.row.images">
                                <img v-bind:src="image.src" width="150" />
                            </li>
                        </ul>
    
                    </p>
                </template>
            </el-table-column>
            <el-table-column label="Tên phòng" prop="title" sortable>
            </el-table-column>
    
            <el-table-column label="Giá phòng" prop="price" sortable>
            </el-table-column>
    
            <el-table-column label="Loại phòng" prop="product_type" sortable>
            </el-table-column>
    
            <el-table-column label="Last edit" prop="published_at" sortable>
            </el-table-column>
    
            <el-table-column label="Edit" width="150"> 
                <template scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index)">Edit</el-button>
                    <el-button type="danger" size="small" @click="handleDel()">Xóa</el-button>
                </template>
            </el-table-column>
    
        </el-table>
    
    </div>
</template>

<script>

import firebase from 'firebase';

let db = firebase.database()
let roomRef = db.ref().child('rooms');

export default {
    firebase: {
        rooms: roomRef
    },
    data() {
        return {
            editFormVisible: false

        }
    },
    components: {
    },
    methods: {
        handleEdit: function (index) {
            alert(index)
        },
    },
    computed: {
        tableData: function () {
            var roomsNew = this.rooms;
            var i = 1;
            roomsNew.forEach(function (e) {
                e.key =e['.key'];
                e.published_at = new Date(e.published_at).toLocaleString();
                e.stt = i++;
            })
            console.log(roomsNew);
            return roomsNew;
            
        },
    }

}
</script>

<style lang="scss">
.list-inline {
    margin: 20px 0;
    list-style: none;
    padding-left: 0;
    li {
        display: inline-block;
        margin: 0 10px;
    }
}
</style>



