<template>
  <el-form label-width="0px" class="login-container" :model="formSignIn" :rules="ruleFormSignIn" ref="formSignIn">
  
    <h3 class="title">Đăng nhập tài khoản {{getUserSignIn}} </h3>
  
    <el-form-item prop="email">
      <el-input type="email" v-model="formSignIn.email" placeholder="Email" auto-complete="off"> </el-input>
    </el-form-item>
  
    <el-form-item prop="password">
      <el-input type="password" v-model="formSignIn.password" placeholder="Password" auto-complete="off"></el-input>
    </el-form-item>
  
    <el-form-item>
      <el-button type="primary" @click.native.prevent="toggleSignIn" :loading="logining">Đăng nhập</el-button>
  
      <el-checkbox v-model="checked" checked class="remember">Remember</el-checkbox>
  
    </el-form-item>

    user: admin@gmail.com
    <br/>
    pass : 123456
  
  </el-form>
</template>

<script>

import firebase from 'firebase'

import { mapGetters } from 'vuex'

import {
  Notification
} from 'element-ui';

let db = firebase.database()

export default {
  name: 'login',
  firebase: {

  },

  data() {
    return {
      user: '',
      logining: false,
      checked: true,
      formSignIn: {
        email: '',
        password: '',
      },
      ruleFormSignIn: {
        email: [
          { required: true, message: 'Vui lòng nhập địa chỉ email', trigger: 'blur' },
          { type: 'email', message: 'Địa chỉ email không hợp lệ', trigger: 'blur,change' }
        ],
        password: [
          { required: true, message: 'Vui lòng nhập password', trigger: 'blur' },
        ]
      }
    }
  },
  computed: {
    ...mapGetters(['getUserSignIn'])

  },
  filters: {

  },
  methods: {

    toggleSignIn() {
      var _this = this;


      if (firebase.auth().currentUser) {
        //Start sign out
        firebase.auth().signOut();
        //End sign out
      } else {

        this.$refs.formSignIn.validate((valid) => {

          if (valid) {

            this.logining = true;

            //Start sign in with email and password
            firebase.auth().signInWithEmailAndPassword(this.formSignIn.email, this.formSignIn.password).catch(function (error) {
              //Handle Error in Firebase
              var errorCode = error.code;
              var errorMessage = error.message;
              //Start Notification

              if (errorCode === 'auth/wrong-password') {
                Notification.warning({
                  title: 'Lỗi xảy ra',
                  message: 'Nhập sai password'
                });
              } else {
                Notification.warning({
                  title: 'Lỗi xảy ra',
                  message: errorMessage
                });
              }
              console.log(error);

            });

          } else {
            console.log("Login fail")
            return false;
          }
        });
      }

    }


  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">


.login-container {
  box-shadow: 0 0 100px rgba(0, 0, 0, .08);
  border-radius: 5px;
  margin: 20px auto 70px;
  max-width: 350px;
  padding: 35px 35px 15px 35px;
  border: 1px solid #eaeaea;
  .title {
    margin: 0px auto 20px auto;
    text-align: center;
    color: #505458;
  }
  .remember {
    margin: 0px 0px 0px 15px;
    text-align: center;
  }
}
</style>



